	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 3 - La nouvelle amie","texte":{"childMarkdownRemark":{"html":"<p>La fille blonde des toilettes m’approche alors que je mangeais tranquillement dans mon coin. Elle me fait un salut de la main.</p>\n<p>Blonde: Salut, je peux m’asseoir?<br>\nGaïa: Heu, oui si tu veux.<br>\nBlonde: Merci.</p>\n<p>Elle s’assied en face de moi et tire un peu sur son uniforme, elle semble inconfortable dedans.</p>\n<p>Yura: En passant, moi c’est Yura, je suis nouvelle ici.<br>\nGaïa: Gaïa, enchantée, j’espère que tu vas te plaire ici.<br>\nYura: Moi aussi, mais l’uniforme n’aide pas.<br>\nGaïa: Tu vas t’habituer rapidement, après ça tu n’y penseras même plus.<br>\nYura: J’espère!</p>\n<p>Elle rigole, clairement, elle n’était pas du pays.</p>\n<p>Gaïa: Tu as déménager ici pourquoi?<br>\nYura: Mon père a changé de boulot et moi bah j’ai pas trop eu le choix de le suivre.<br>\nGaïa: Je vois.</p>\n<p>Je prend une bouchée de mon sandwich et regarde devant elle.</p>\n<p>Gaïa: T’as un dîner?<br>\nYura: Non, appétit de moineau.<br>\nGaïa: Oh, tu n’as même pas un peu faim?<br>\nYura: Nan~</p>\n<p>J’hoche doucement la tête, pas besoin d’insister.</p>\n<p>Yura: Ça te dérangerait de me faire visiter l’école après avoir mangé?<br>\nGaïa: Sans soucis.</p>\n<p>Je prend une autre bouché et Yura regarde mes ongles. Je les cache par réflexe.</p>\n<p>Yura: C’est jolie tes ongles, un beau verni vert nature.<br>\nGaïa: Ah bon? Tu aimes bien? Bah merci.</p>\n<p>Je souris, rassurée qu’elle pense que c’est du verni. Au bout de cinq minutes, j’ai fini de manger et range mes plats dans ma boîte à lunch.</p>\n<p>Gaïa: Je vais porter ça dans mon casier et ensuite, je te fais visiter l’école?<br>\nYura: Cool! T’es trop sympa!<br>\nGaïa: Merci Yura.</p>\n<p>Yura sourit et regarde autour d’elle pendant que je fini de ranger mes plats. Je me lève et Yura fait pareil. Elle manque de tomber à cause de la jupe et se plaint un peu. Je rigole doucement en plaçant ma main devant ma bouche. Yura me regarde et éclate de rire. Elle se redresse et sourit.</p>\n<p>Yura: T’as vu comment j’ai presque fini face la première au sol? Ça aurait été de toute élégance!<br>\nGaïa: Ouais, tu dois vraiment t’habituer à l’uniforme.</p>\n<p>Yura sourit et hoche la tête. On va finalement à mon casier, j’y range mes choses.</p>\n<p>Avant la fin de l’heure du dîner, j’avais fini de faire le tour de l’école avec Yura. Elle était très sympathique et amusante. On s’échange nos numéros de téléphone pour se rejoindre après l’école.</p>\n<p>Je pense avoir enfin réussi à avoir une amie qui ne m’apprécie pas que pour mes bonnes notes en classes. En fait, Yura est une fille très intelligente, peut-être même plus que moi. Yura part de son côté en me faisant signe de la main, je lui rend le signe. Je retourne à mon casier pour prendre les livres de mes cours de l’après midi.</p>\n<p>À la fin de la journée, on se retrouve devant la jardin intérieur, j’ai l’impression que mon pouvoir s’est calmé, c’est rassurant. Yura parlait avec un gars de l’équipe de sport, elle avait croisé les bras et était adossée à un casier. Yura me voit approcher et fait salut au mec. Il me regarde et regarde Yura. Il part sans demander son reste. Yura s’approche et tend son bras pour qu’on se place bras dessus, bras dessous. Je souris et me place.</p>\n<p>Yura: On va chez moi? J’ai quelques boîtes à défaire, mais rien d’urgent.<br>\nGaïa: Si tu veux, je vais juste avertir mes parents que je vais chez une amie avant de rentrer.<br>\nYura: Dac!</p>\n<p>J’envoie un texto à ma mère et mon père et on part vers chez Yura. Yura voulait prendre le bus, mais elle change d’idée en voyant la quantité de monde qui veulent faire la même chose. Je lui propose de marcher, elle sourit et on commence à marcher. Elle ne restait pas très loin de chez moi en fait. À peine 20 minutes de marches séparaient nos maisons.</p>\n<p>J’entre chez elle et retire mes souliers, comme elle le fait.</p>\n<p>Yura: Je vais aller me changer si tu me permets, je suis déjà fatiguée de l’uniforme.<br>\nGaïa: Vas-y fait comme chez toi.<br>\nYura: Bah je suis chez moi.</p>\n<p>Elle me fait un clin d’oeil et part vers ce que je peux assumer être sa chambre. Elle commençait déjà à déboutonner sa chemise. Je regarde autour de moi, plusieurs boîtes étaient empilées. Je m’avance et m’assied sur une chaise de la cuisine.</p>\n<p>Yura arrive quelques minutes plus tard, jeans et camisole comme habit, les cheveux détachés en plus. Je souris en la voyant.</p>\n<p>Gaïa: Je vois que tu as l’air plus à l'aise.<br>\nYura: Tellement! Tu sais pas comment ça fait un bien. Je ne risque plus de me casser la figure en tombant à cause de la jupe!<br>\nGaïa: Je te crois. Donc on fait quoi?<br>\nYura: Avant je veux savoir un truc.<br>\nGaïa: Vas y?<br>\nYura: Tes ongles verts, c’est pas du verni c’est bien ça?<br>\nGaïa: Heu, je, oui c’est du verni… Pourquoi ça ne le serais pas?<br>\nYura: Je pense que tu as un pouvoir ou un truc comme ça~<br>\nGaïa: Quoi? Un pouvoir? Comme les gens dans les films et les histoires?<br>\nYura: Bon, t’es pas très bonne pour mentir, mais ça se pratique. Alors, ton pouvoir c’est quoi?<br>\nGaïa: Comment tu sais?<br>\nYura: Tu sais ce qu’on dit, on sait reconnaître nos particularités sur les autres.<br>\nGaïa: Je comprend pas trop, tu as un pouvoir toi aussi?<br>\nYura: Ouaip.<br>\nGaïa: Oh…<br>\nYura: En fait, on est pas les seules à avoir un pouvoir, mon copain en a un aussi et on monte un groupe de gens avec des pouvoirs.<br>\nGaïa: Sérieux?<br>\nYura: Ouais, quand on aura assez de gens, on pourra organiser des vols de banque et pleins de trucs amusants~<br>\nGaïa: Comment ça des vols de banque? Je participerais pas à ça moi!</p>\n<p>Je me lève de la chaise et commence à aller vers la porte. Je sens la main de Yura qui m’attrape le poignet. Je commence à crier et me débattre, mais elle est si forte. Elle me plaque face au sol et s’assied sur moi. J’essaie de me défaire de sa prise.</p>\n<p>La porte de l’appartement s’ouvre et je vois un paire de chaussure s'approcher.</p>\n<p>Gaïa: À l’aide! Elle me veut du mal!</p>\n<p>L’homme se met à genou devant moi et Yura me redresse pour que je sois assise aussi. J’essaie de me débattre, l’homme était du côté de Yura.</p>\n<p>L’homme approche ses pouces de mon front et soudain, le blanc total.</p>"}},"chapitreAvant":"chapitre-2-perturbations-en-classe","chapitreApres":null,"nomRoman":"la-puissance-de-la-nature","slug":"chapitre-3-la-nouvelle-amie","codeChapitre":"H0006C003","equivalentUrl":"/stories/chapter/chapter-3-new-friend"}},"pathContext":{"slug":"chapitre-3-la-nouvelle-amie"}}

